.library-switch {
  display: inline-flex;
  border-radius: var(--radius-pill);
  padding: 4px;
  background: rgba(10, 18, 32, 0.78);
  border: 1px solid var(--border);
  gap: 6px;
}

.library-switch button {
  border: none;
  background: transparent;
  color: var(--muted);
  font-size: 13px;
  font-weight: 600;
  padding: 8px 14px;
  border-radius: var(--radius-pill);
  transition: color var(--transition-base), background var(--transition-base), box-shadow var(--transition-base);
}

.library-switch button:hover,
.library-switch button:focus-visible {
  color: var(--fg);
}

.library-switch button.active {
  background: var(--accent);
  color: var(--accent-contrast);
}

.settings-overlay {
  position: fixed;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 32px 16px;
  background: var(--bg-overlay);
  backdrop-filter: blur(14px);
  z-index: 950;
  overflow-y: auto;
}

.settings-overlay[hidden] {
  display: none;
}

.settings-dialog {
  width: min(520px, 100%);
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 28px;
  border-radius: var(--radius-lg);
  border: 1px solid rgba(126, 166, 255, 0.32);
  background: linear-gradient(165deg, rgba(12, 18, 32, 0.96), rgba(8, 14, 26, 0.92));
  box-shadow: 0 30px 60px rgba(5, 12, 26, 0.6);
  max-height: calc(100vh - 64px);
  overflow: auto;
}

.settings-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.settings-title {
  font-size: 24px;
  letter-spacing: 0.02em;
}

.settings-body {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.settings-row {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.settings-inline {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: center;
}

.settings-inline > input[type="checkbox"],
.settings-inline > input[type="radio"] {
  accent-color: var(--accent);
}

.settings-dialog input[type="password"] {
  width: 100%;
  padding: 12px 16px;
  border-radius: var(--radius-md);
  border: 1px solid rgba(126, 166, 255, 0.24);
  background: rgba(12, 18, 32, 0.9);
  color: var(--fg);
  transition: border-color var(--transition-base), box-shadow var(--transition-base), background var(--transition-base);
}

.settings-dialog input[type="password"]:hover,
.settings-dialog input[type="password"]:focus-visible {
  border-color: var(--border-strong);
  background: rgba(16, 24, 40, 0.95);
  box-shadow: var(--shadow-ring);
}

.settings-help {
  font-size: 14px;
  color: var(--muted);
  line-height: 1.5;
}

.settings-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

.hero__cta,
.btn,
.watchlist-button,
.settings-button,
.toggle-advanced,
.watchlist-actions > button,
.settings-actions > button,
#yearReset,
#closeWatchlist,
#clearWatchlist,
#exportWatchlist,
#settingsClose2,
#tmdbSave,
#tmdbTest,
#tmdbClearCache,
#resetFilters,
#toggleAdvanced,
#watchlistToggle,
#settingsBtn {
  border-radius: var(--radius-pill);
  border: 1px solid transparent;
  padding: 11px 20px;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 0.008em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: transform var(--transition-smooth), background-color var(--transition-smooth), opacity var(--transition-smooth), border-color var(--transition-smooth), color var(--transition-smooth), box-shadow var(--transition-smooth);
  background-color: rgba(16, 22, 34, 0.88);
  color: var(--fg);
}

.hero__cta,
.btn.primary,
.primary,
.watchlist-actions > button.primary,
#tmdbSave,
#exportWatchlist {
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-strong) 100%);
  color: var(--accent-contrast);
  border-color: transparent;
  box-shadow: 0 4px 16px rgba(107, 148, 224, 0.28);
}

.btn,
.watchlist-button,
.settings-button,
.toggle-advanced,
.watchlist-actions > button:not(.primary),
.settings-actions > button,
#yearReset,
#closeWatchlist,
#clearWatchlist,
#settingsClose2,
#tmdbTest,
#tmdbClearCache,
#resetFilters,
#toggleAdvanced,
#watchlistToggle,
#settingsBtn {
  background-color: rgba(18, 26, 42, 0.88);
  border-color: var(--border);
  color: var(--fg);
}

.hero__cta:hover,
.btn.primary:hover,
.primary:hover,
.watchlist-actions > button.primary:hover,
#tmdbSave:hover,
#exportWatchlist:hover {
  transform: translateY(-2px);
  background: linear-gradient(135deg, var(--accent-strong) 0%, #4a6fb8 100%);
  box-shadow: 0 6px 24px rgba(107, 148, 224, 0.38);
}

.btn:hover,
.watchlist-button:hover,
.settings-button:hover,
.toggle-advanced:hover,
.watchlist-actions > button:hover,
.settings-actions > button:hover,
#yearReset:hover,
#closeWatchlist:hover,
#clearWatchlist:hover,
#settingsClose2:hover,
#tmdbTest:hover,
#tmdbClearCache:hover,
#resetFilters:hover,
#toggleAdvanced:hover,
#watchlistToggle:hover,
#settingsBtn:hover,
.hero__cta:focus-visible,
.btn:focus-visible,
.watchlist-button:focus-visible,
.settings-button:focus-visible,
.toggle-advanced:focus-visible,
.watchlist-actions > button:focus-visible,
.settings-actions > button:focus-visible,
#yearReset:focus-visible,
#closeWatchlist:focus-visible,
#clearWatchlist:focus-visible,
#settingsClose2:focus-visible,
#tmdbTest:focus-visible,
#tmdbClearCache:focus-visible,
#resetFilters:focus-visible,
#toggleAdvanced:focus-visible,
#watchlistToggle:focus-visible,
#settingsBtn:focus-visible {
  border-color: var(--border-strong);
}

.hero__cta:focus-visible,
.btn:focus-visible,
.watchlist-button:focus-visible,
.settings-button:focus-visible,
.toggle-advanced:focus-visible,
.watchlist-actions > button:focus-visible,
.settings-actions > button:focus-visible,
#yearReset:focus-visible,
#closeWatchlist:focus-visible,
#clearWatchlist:focus-visible,
#settingsClose2:focus-visible,
#tmdbTest:focus-visible,
#tmdbClearCache:focus-visible,
#resetFilters:focus-visible,
#toggleAdvanced:focus-visible,
#watchlistToggle:focus-visible,
#settingsBtn:focus-visible,
#tmdbSave:focus-visible,
#exportWatchlist:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

.btn:disabled,
.watchlist-button:disabled,
.settings-button:disabled,
.toggle-advanced:disabled,
.watchlist-actions > button:disabled,
.settings-actions > button:disabled,
#yearReset:disabled,
#closeWatchlist:disabled,
#clearWatchlist:disabled,
#settingsClose2:disabled,
#tmdbSave:disabled,
#tmdbTest:disabled,
#tmdbClearCache:disabled,
#resetFilters:disabled,
#toggleAdvanced:disabled,
#watchlistToggle:disabled,
#settingsBtn:disabled,
#exportWatchlist:disabled {
  opacity: 0.55;
  cursor: not-allowed;
}

.btn-link {
  background: none;
  border: none;
  padding: 0;
  color: var(--accent);
  font-weight: 500;
  transition: color var(--transition-base);
  font: inherit;
  cursor: pointer;
}

.btn-link:hover,
.btn-link:focus-visible {
  color: var(--fg);
}

.settings-label {
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
}

.field {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: rgba(12, 16, 24, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: var(--radius-md);
  padding: 7px 10px;
  min-height: 36px;
  opacity: 0.92;
  transition: background-color var(--transition-smooth), border-color var(--transition-smooth), opacity var(--transition-smooth);
}

.field:hover,
.field:focus-within {
  border-color: rgba(255, 255, 255, 0.16);
  background: rgba(14, 19, 28, 0.85);
  opacity: 1;
}

.field .icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.05);
  color: rgba(201, 216, 248, 0.7);
  transition: inherit;
}

.field .icon svg {
  width: 14px;
  height: 14px;
  stroke: currentColor;
  stroke-width: 2;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
}

/* Ensure small icons also inside the filter chips header */
.filters-chips .icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: clamp(26px, 4.2vh, 36px);
  height: clamp(26px, 4.2vh, 36px);
  border-radius: var(--radius-sm);
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(255, 255, 255, 0.08);
  color: rgba(201, 216, 248, 0.82);
}

.filters-chips .icon svg {
  width: clamp(14px, 2.4vh, 18px);
  height: clamp(14px, 2.4vh, 18px);
  stroke: currentColor;
  stroke-width: 1.8;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  display: block;
}

.field input,
.field select,
.field button {
  background: transparent;
  border: none;
  color: inherit;
  width: 100%;
  font-size: 13px;
  line-height: 1;
  padding: 0;
  margin: 0;
  vertical-align: middle;
}

.field select {
  border-radius: var(--radius-sm);
  padding: 4px 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(14, 20, 32, 0.7);
  transition: border-color var(--transition-smooth), background-color var(--transition-smooth), opacity var(--transition-smooth);
  opacity: 0.94;
}

.field select:hover,
.field select:focus-visible {
  border-color: rgba(255, 255, 255, 0.18);
  background: rgba(14, 20, 32, 0.9);
  opacity: 1;
}

.field button {
  width: auto;
  color: var(--muted);
  opacity: 0.85;
  transition: color var(--transition-base), opacity var(--transition-base);
}

.field button:hover,
.field button:focus-visible {
  color: var(--fg);
  opacity: 1;
}

.toggle-advanced {
  background: rgba(255, 255, 255, 0.04);
  border-color: rgba(255, 255, 255, 0.1);
  color: var(--fg-secondary);
  opacity: 0.88;
  box-shadow: none;
  font-size: 13px;
  padding: 8px 14px;
  min-height: 36px;
  transition: background-color var(--transition-smooth), border-color var(--transition-smooth), opacity var(--transition-smooth), color var(--transition-smooth);
}

.toggle-advanced:hover,
.toggle-advanced:focus-visible {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.18);
  opacity: 1;
  color: var(--fg);
  box-shadow: none;
}

.toggle-advanced:focus-visible {
  outline-offset: 3px;
}

.toggle-advanced:disabled {
  opacity: 0.5;
}

.toggle-chip {
  position: relative;
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.2;
}

.toggle-chip input {
  position: absolute;
  opacity: 0;
  inset: 0;
  cursor: pointer;
}

.toggle-chip span {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  border-radius: var(--radius-pill);
  border: 1px solid var(--border);
  background: rgba(14, 22, 38, 0.9);
  color: var(--muted);
  transition: border-color var(--transition-base), background var(--transition-base), color var(--transition-base), box-shadow var(--transition-base);
}

.toggle-chip input:checked + span {
  background: var(--accent-soft);
  border-color: var(--border-strong);
  color: var(--fg);
  box-shadow: 0 10px 22px rgba(74, 120, 210, 0.26);
}

.toggle-chip:focus-within span {
  box-shadow: var(--shadow-ring);
}

.chip-group,
.chips {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.chip {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  background: var(--chip);
  border: 1px solid var(--border);
  border-radius: var(--radius-pill);
  padding: 6px 12px;
  font-size: 13px;
  color: var(--fg);
  transition: transform var(--transition-base), box-shadow var(--transition-base);
}

.chip.more {
  opacity: 0.85;
  cursor: pointer;
  border-style: dashed;
}

.chip:hover {
  transform: translateY(-1px);
  box-shadow: 0 14px 24px rgba(5, 10, 20, 0.35);
}

.badge {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 2px 8px;
  font-size: 12px;
  letter-spacing: 0.04em;
  border-radius: var(--radius-pill);
  border: 1px solid rgba(126, 166, 255, 0.4);
  background: rgba(18, 32, 58, 0.7);
  color: var(--fg);
}

.cardv2,
.card {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: var(--radius-lg);
  overflow: hidden;
  background: linear-gradient(180deg, rgba(14, 19, 28, 0.92) 0%, rgba(12, 16, 24, 0.94) 60%, rgba(10, 14, 20, 0.96) 100%);
  border: 1px solid rgba(100, 130, 180, 0.06);
  box-shadow: var(--shadow-card);
  transition: transform var(--transition-smooth), box-shadow var(--transition-smooth), border-color var(--transition-smooth);
}

.cardv2::before,
.card::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(107, 148, 224, 0.12), rgba(10, 15, 22, 0.08));
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--transition-smooth);
}

.cardv2:hover,
.cardv2:focus-within,
.card:hover,
.card:focus-within {
  transform: translateY(-3px);
  box-shadow: 0 12px 42px rgba(2, 6, 14, 0.45);
  border-color: rgba(107, 148, 224, 0.14);
}

.cardv2:hover::before,
.cardv2:focus-within::before,
.card:hover::before,
.card:focus-within::before {
  opacity: 0.75;
}

.cardv2__thumb,
.card .poster {
  position: relative;
  background: rgba(8, 12, 20, 0.9);
  overflow: hidden;
}

.cardv2__thumb {
  aspect-ratio: 3 / 4;
}

.card .poster {
  aspect-ratio: 2 / 3;
}

.cardv2__thumb img,
.card .poster img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform var(--transition-base);
}

.cardv2__thumb-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(6, 10, 18, 0.1) 0%, rgba(6, 10, 18, 0.78) 70%, rgba(4, 6, 12, 0.92) 100%);
  opacity: 0.78;
  pointer-events: none;
  transition: opacity var(--transition-base);
  z-index: 1;
}

.cardv2:hover .cardv2__thumb img,
.card:hover .poster img {
  transform: scale(1.04);
}

.cardv2:hover .cardv2__thumb-overlay,
.cardv2:focus-within .cardv2__thumb-overlay {
  opacity: 0.92;
}

.cardv2__progress,
.card .progress {
  position: absolute;
  inset-inline: 0;
  bottom: 0;
  height: 3px;
  background: rgba(255, 255, 255, 0.08);
}

.cardv2__progress > span,
.card .progress > span {
  display: block;
  height: 100%;
  background: linear-gradient(90deg, var(--accent), #f9b233);
}

.cardv2__body,
.card .meta {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 16px;
}

.cardv2__title,
.card .title {
  font-family: var(--font-display);
  font-weight: 600;
  font-size: 17px;
  line-height: 1.3;
  letter-spacing: -0.008em;
  color: var(--fg);
  margin: 0;
}

.cardv2__meta,
.card .sub {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  font-size: 13px;
  color: var(--muted);
  opacity: 0.9;
}

.cardv2__tags,
.card .chips {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.cardv2__actions,
.card .card-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-top: auto;
}

.cardv2__actions .btn,
.card .card-actions .btn {
  flex: 1 1 auto;
}

.cardv2__badge-group,
.card .badge-group {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.cardv2__badge-group {
  position: absolute;
  inset: 12px auto auto 12px;
  z-index: 2;
  pointer-events: none;
}

.cardv2__badge-group .badge {
  pointer-events: auto;
}

.watch-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  border-radius: var(--radius-pill);
  border: 1px solid var(--border);
  background: rgba(14, 22, 38, 0.9);
  color: var(--fg);
  padding: 8px 14px;
  font-size: 13px;
  font-weight: 600;
  transition: background var(--transition-base), border-color var(--transition-base), color var(--transition-base), box-shadow var(--transition-base);
}

.watch-btn:hover,
.watch-btn:focus-visible {
  border-color: var(--border-strong);
  box-shadow: var(--shadow-ring);
}

.watch-btn.active {
  background: var(--accent-soft);
  color: var(--accent);
  border-color: var(--border-strong);
  box-shadow: 0 12px 26px rgba(94, 142, 255, 0.32);
}

.segment-control {
  display: inline-flex;
  align-items: center;
  border-radius: var(--radius-pill);
  background: rgba(10, 16, 28, 0.8);
  border: 1px solid var(--border);
  padding: 4px;
  gap: 4px;
}

.segment-control button {
  position: relative;
  flex: 1;
  border: none;
  background: transparent;
  color: var(--muted);
  font-weight: 600;
  padding: 8px 14px;
  border-radius: var(--radius-pill);
  transition: color var(--transition-base), background var(--transition-base), box-shadow var(--transition-base);
}

.segment-control button:hover,
.segment-control button:focus-visible {
  color: var(--fg);
}

.segment-control button[aria-pressed="true"],
.segment-control button.active {
  background: linear-gradient(135deg, var(--accent), var(--accent-strong));
  color: var(--accent-contrast);
  box-shadow: 0 12px 26px rgba(94, 142, 255, 0.32);
}

.checkbox {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
}

.checkbox input[type="checkbox"] {
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 6px;
  border: 2px solid var(--border);
  background: rgba(10, 18, 30, 0.9);
  position: relative;
  transition: border-color var(--transition-base), background var(--transition-base), box-shadow var(--transition-base);
}

.checkbox input[type="checkbox"]:checked {
  background: var(--accent);
  border-color: var(--accent-strong);
  box-shadow: 0 10px 20px rgba(94, 142, 255, 0.32);
}

.checkbox input[type="checkbox"]:checked::after {
  content: "";
  position: absolute;
  inset: 3px;
  border-radius: 3px;
  background: var(--accent-contrast);
}

.checkbox input[type="checkbox"]:focus-visible {
  box-shadow: var(--shadow-ring);
}

.watchlist-items {
  list-style: none;
  margin: 16px 0 0 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.watchlist-item {
  display: flex;
  align-items: center;
  gap: 12px;
  background: rgba(10, 18, 32, 0.72);
  border-radius: var(--radius-md);
  padding: 12px 14px;
  border: 1px solid rgba(126, 166, 255, 0.12);
}

.watchlist-item .title {
  font-weight: 600;
  color: var(--fg);
}

@media (max-width: 768px) {
  .cardv2__actions {
    flex-direction: column;
    align-items: stretch;
  }

  .cardv2__actions .btn {
    width: 100%;
  }
}

@media (prefers-reduced-motion: reduce) {
  .cardv2,
  .cardv2 *,
  .library-switch button,
  .segment-control button,
  .chip,
  .toggle-chip span,
  .hero__cta,
  .btn,
  .watchlist-button,
  .settings-button,
  .toggle-advanced {
    transition-duration: 0ms !important;
    animation-duration: 0ms !important;
  }

  .cardv2:hover,
  .chip:hover,
  .hero__cta:hover,
  .btn:hover {
    transform: none;
    box-shadow: none;
  }
}
